progress {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-progress-appearance: none;
    margin: 0;
    padding: 0;
    height: 2px;
    position: fixed;
    z-index: 3;
    width: 100%;
    &::-webkit-progress-bar {
        background-color:transparent;
    }
    &::-webkit-progress-value{
        background-color: #73B145;
    }
}